<template>
  <div id="app">
    <!-- <img alt="Vue logo" src="./assets/img/logo.png"> -->
    <button @click="gorouter()">跳转pdf</button>
    <button @click="gorouter('recorderDemo')">跳转shipin</button>
    <button @click="gorouter('luyin')">跳转录音</button>
    <router-view />
  </div>  
</template>

<script>

export default {
  name: 'App',
  data(){
    return{}
  },
  mounted(){},
  methods: {
    gorouter(v) {
      if(v) {
        this.$router.push({name: v});
      }else {
        this.$router.push({name: 'pdf'});
      }
    }
  }
}
</script>

